



































































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import moment from 'moment';
import CSelectorYMD from '@/components/CSelectorYMD.vue';
import CDatetime from '@/components/links/CDatetime.vue';
import MomentUtil from '@/typescript/MomentUtil.ts';

@Component({
  components: {
    CSelectorYMD,
    CDatetime,
  },
  head: {
    title: {
      inner: '年齢計算',
    },
  },
})
export default class Age extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataNowMoment: any = moment(new Date());
  private dataYearTerms: {
    start: number,
    end: number,
  } = {
    start: -120,
    end: 0,
  };
  private dataBirthdayPoint: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  };
  private dataPointDiff: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  };
  private dataTotalPointDiff: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  };
  private dataAlert: string = '';

  @Watch('dataNowMoment')
  onNowMomentChange(newValue: string, oldValue: string): void {
    this.diffDatetime();
  }

  // Lifecycle hook
  created(): void {
    this.dataBirthdayPoint = MomentUtil.toDatePoint(this.dataNowMoment.clone().startOf('years'));
    this.getNow();
  }

  // Methods
  private getNow(): void {
    setInterval((): void => {
      let now: any = moment(new Date());
      if (this.dataNowMoment !== now) {
        this.dataNowMoment = now;
      }
    }, 500);
  }
  private calcNaturalMonth(month: number): number {
    return month + 1;
  }
  private diffDatetime(): void {
    let nowMoment: any = this.dataNowMoment.clone();
    let birthdayMoment: any = MomentUtil.toDateMoment(this.dataBirthdayPoint);
    if (0 < birthdayMoment.diff(nowMoment)) {
      this.dataAlert = '誕生日は現在よりも前の時刻を設定してください。';
    } else {
      this.dataAlert = '';
      this.dataPointDiff = MomentUtil.diffDateMomentsToEachPointDiff(birthdayMoment, nowMoment);
      this.dataTotalPointDiff = MomentUtil.diffDateMomentsToTotalPointDiff(birthdayMoment, nowMoment);
    }
  }
  private listenEventPointYearChange($event: Event): void {
    Vue.set(this.dataBirthdayPoint, 'year', Number($event));
  }
  private listenEventPointMonthChange($event: Event): void {
    Vue.set(this.dataBirthdayPoint, 'month', Number($event));
  }
  private listenEventPointDayChange($event: Event): void {
    Vue.set(this.dataBirthdayPoint, 'month', Number($event));
  }
}
