
















import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import moment from 'moment';
import MomentUtil from '@/typescript/MomentUtil.ts'

@Component
export default class CSelectorYMD extends Vue {
  // Props
  @Prop() public pointYear!: number;
  @Prop() public pointMonth!: number;
  @Prop() public pointDay!: number;
  @Prop() public yearTerms!: {
    start: number,
    end: number,
  };

  // Data
  private dataYearTerms: {
    start: number,
    end: number,
  } = this.yearTerms;
  private dataYears: number[] = [];
  private dataMonths: number[] = [];
  private dataDays: number[] = [];
  private dataNowMoment: any = moment(new Date());
  private dataPoint: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } = {
    year: this.pointYear,
    month: this.pointMonth,
    day: this.pointDay,
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  };

  // Lifecycle hook
  created(): void {
    this.initYears();
    this.initMonths();
    this.updateDays(this.dataNowMoment, this.dataDays);
  }

  // Emit
  @Emit('selectYear')
  private emitSelectYear(): number {
    return this.dataPoint.year;
  }
  @Emit('selectMonth')
  private emitSelectMonth(): number {
    return this.dataPoint.month;
  }
  @Emit('selectDay')
  private emitSelectDay(): number {
    return this.dataPoint.day;
  }

  // Methods
  private listenOnSelectYear($event: Event): void {
    Vue.set(this.dataPoint, 'year', Number((<HTMLInputElement>(<any>$event).target).value));
    let dateMoment: any = MomentUtil.toDateMoment(this.dataPoint);
    this.updateDays(dateMoment, this.dataDays);
    this.emitSelectYear();
  }
  private listenOnSelectMonth($event: Event): void {
    Vue.set(this.dataPoint, 'month', Number((<HTMLInputElement>(<any>$event).target).value));
    let dateMoment: any = MomentUtil.toDateMoment(this.dataPoint);
    this.updateDays(dateMoment, this.dataDays);
    this.emitSelectMonth();
  }
  private listenOnSelectDay($event: Event): void {
    Vue.set(this.dataPoint, 'day', Number((<HTMLInputElement>(<any>$event).target).value));
    this.emitSelectDay();
  }
  private initYears(): void {
    let startPointYear: number = this.dataNowMoment.clone().add(this.dataYearTerms.start, 'years').year();
    let endPointYear: number = this.dataNowMoment.clone().add(this.dataYearTerms.end, 'years').year();
    let i: number = 0;
    while (startPointYear + i <= endPointYear) {
      this.dataYears.push(startPointYear + i);
      i++;
    }
  }
  private initMonths(): void {
    const MONTH_TERM: number = 12;
    for (let i: number = 0; i < MONTH_TERM; i++) {
      this.dataMonths.push(i);
    }
  }
  private calcNaturalMonth(month: number): number {
    return month + 1;
  }
  private updateDays(dateMoment: any, days: number[]): void {
    let endDayOfMonth: number = dateMoment.clone().endOf('month').date();
    if (endDayOfMonth < this.dataPoint.day) {
      Vue.set(this.dataPoint, 'day', endDayOfMonth);
    }
    if (days.length < endDayOfMonth) {
      for (let i: number = days.length + 1; i <= endDayOfMonth; i++) {
        days.push(i);
      }
    } else if (endDayOfMonth < days.length) {
      for (let i: number = days.length; endDayOfMonth < i; i--) {
        days.pop();
      }
    }
  }
}
